import React, { Component } from 'react';
import { Button } from 'react-bootstrap';
import Table from 'react-bootstrap/Table';

class QuoteDisplay extends Component {
   constructor(props) {
      super(props);
      this.state = { showDate: false, showVolume: false, showDiff: false}
   }

   toggleShowDate = (status) => {      
      status ? this.setState({showDate: false}) : this.setState({showDate: true});
   } 

   toggleShowVolume = (status) => {      
      status ? this.setState({showVolume: false}) : this.setState({showVolume: true});
   }

   toggleShowDiff = (status) => {      
      status ? this.setState({showDiff: false}) : this.setState({showDiff: true});
   }

   render() {
      //const { data } = this.props;
      //console.log("ok");
      //console.log("Show Date: " + this.state.showDate);
      return (
         <Table striped bordered hover responsive="sm" size="sm">
            <DisplayHeader 
               showStates={this.state} 
               handleToggleShowDate={this.toggleShowDate} 
               handleToggleShowVolume={this.toggleShowVolume}
               handleToggleShowDiff={this.toggleShowDiff}
            />
            <DisplayBody 
               rows={this.props.data} 
               showStates={this.state}
               handleSubmit={this.props.handleSubmit}
            />
         </Table>
      )
   }
}

const DisplayHeader = props => {
   //console.log(props);
   return (
      <thead>
         <tr className="text-right"> 
            <th className="text-left" style={{width: '25%'}}>
            <Button style={{border: 'none', background: 'none', padding: 0}} disabled><span role="img" aria-label="Symbol">&#x1F4C8;</span></Button>
            </th>
            <th style={{width: '25%'}}>
               {props.showStates.showDate ? <Button className="shadow-none" style={{border: 'none', background: 'none', padding: 0}}  onClick={() => props.handleToggleShowDate(props.showStates.showDate)}><span role="img" aria-label="Date">&#x1F4C6;</span></Button> : <Button className="shadow-none"  style={{border: 'none', background: 'none', padding: 0}} onClick={() => props.handleToggleShowDate(props.showStates.showDate)}><span role="img" aria-label="Time">&#x23F2;</span></Button> }
            </th>
            <th style={{width: '25%'}}>
               {props.showStates.showVolume ? <Button className="shadow-none" style={{border: 'none', background: 'none', padding: 0}} onClick={() => props.handleToggleShowVolume(props.showStates.showVolume)}><span role="img" aria-label="Volume">&#x1F4CA;</span></Button> : <Button className="shadow-none" style={{border: 'none', background: 'none', padding: 0}} onClick={() => props.handleToggleShowVolume(props.showStates.showVolume)}><span role="img" aria-label="Price">&#x1F4B0;</span></Button> }
            </th>
            <th style={{width: '25%'}}>{props.showStates.showDiff ? <Button className="shadow-none" style={{border: 'none', background: 'none', color: 'black', padding: 0}} onClick={() => props.handleToggleShowDiff(props.showStates.showDiff)}><span>&#x00B1;</span></Button> : <Button className="shadow-none" style={{border: 'none', background: 'none', color: 'black', padding: 0}} onClick={() => props.handleToggleShowDiff(props.showStates.showDiff)}><span>%</span></Button>}</th>
         </tr>
      </thead>
   )
};

class DisplayBody extends Component {

   handleRowSubmit = (exchange, symbol) => {
      this.props.handleSubmit(symbol.toLowerCase(), exchange.toLowerCase(), false);
   }

   render() {
      const rows = this.props.rows.map((row, index) => {
         const dateOptions = { month: 'numeric', day: 'numeric' };
         const dateStr = new Date(row.regularMarketTime.raw*1000).toLocaleDateString('en-CA', dateOptions);
         const timeOptions = { hour12: false };
         const timeStr = new Date(row.regularMarketTime.raw*1000).toLocaleTimeString('en-CA', timeOptions);
         const changePercent = row.regularMarketChangePercent.fmt;
         const change = row.regularMarketChange.fmt;
         const textColor = row.regularMarketChange.raw === 0 ? "text-dark"
            : (row.regularMarketChange.raw > 0 ? "text-success" : "text-danger");
         const colonIndex = row.symbol.indexOf('.');
         const price = row.regularMarketPrice.fmt;
         const volume = row.regularMarketVolume.fmt;
   
         const symColor = colonIndex > -1 ? 
            (row.symbol.slice(colonIndex+1).toLowerCase() === 'to' ? "text-primary" : 
              (row.symbol.slice(colonIndex+1).toLowerCase() === 'cn' ? "text-danger" : 
                 (row.symbol.slice(colonIndex+1).toLowerCase() === 'v' ? "text-dark" : 
                   "text-warning" 
                 )
              )           
            ) : "text-success";
         
         const exchange = colonIndex > -1 ? 
            (row.symbol.slice(colonIndex+1).toLowerCase() === 'to' ? "tsx" : 
              (row.symbol.slice(colonIndex+1).toLowerCase() === 'cn' ? "cse" : 
                 (row.symbol.slice(colonIndex+1).toLowerCase() === 'v' ? "tsxv" : 
                   "neo" 
                 )
              )           
            ) : "us";
   
   
         const symbolString = colonIndex > -1 ? row.symbol.slice(0,colonIndex) : row.symbol;
         const currency = row.currency.toUpperCase() === 'CAD' ? "C$" : "$" ;  
   
         return (
   
            <tr key={exchange+"-"+symbolString} className="text-right" onClick={() => this.handleRowSubmit(exchange, symbolString)}>
               <td className={symColor + " text-left font-weight-bold"}>{symbolString}</td>
               <td>
               {this.props.showStates.showDate ? <span>{dateStr}</span> : <span>{timeStr}</span>}
               </td>
               <td>
                  {this.props.showStates.showVolume ? <span>{volume}</span>: <span>{currency}{price}</span> }
               </td>
               <td className={textColor}>
                  {this.props.showStates.showDiff ? <span>{change}</span> : <span>{changePercent}</span> }
               </td>
            </tr>
         )
      });
   
      return <tbody>{rows}</tbody>;
   }
}

export default QuoteDisplay;
